<template>
	<view class="warp" v-if="quan == 1">
		 
		<!-- #ifdef APP-PLUS -->
		<statusBar></statusBar>
		<!-- #endif -->
		
		<!-- banner -->
		<unicloud-db ref="bannerdb" v-slot:default="{data, loading, error, options}" collection="opendb-banner"
			field="_id,bannerfile,open_url,title" @load="onqueryload">
			<!-- 当无banner数据时显示占位图 -->
			<image v-if="!(loading||data.length)" class="banner-image" src="/static/uni-center/headers.png" mode="aspectFill" :draggable="false" />
			
			<swiper v-else class="swiper-box" @change="changeSwiper" :current="current" indicator-dots>
				<swiper-item v-for="(item, index) in bannerList" :key="item._id">
					<!-- 如果banner的状态为true则显示 -->
					<image class="banner-image" :src="item.bannerfile.url" mode="aspectFill" :draggable="false" />
					
					
				</swiper-item>
			</swiper>
		</unicloud-db>

		<!-- 快捷入口 -->
		<view class="section-box">
			<text class="decoration"></text>
			<text class="section-text">快捷入口</text>
		</view>
		
		<view class="fastEntry">
			<view class="left" >
				
				<button @click="goQuestion" type="primary" size="default" style="background-color: #E5C4C4; color: #fff;">问卷调查</button>
				
			</view>
			<view class="right" v-if="videobool == 1">
				<button @click="goList" type="warn" size="default" style="background-color: #C9A6A6; color: #fff;">视频列表</button>
			</view>
			<!-- <view class="login">
				<button open-type="getPhoneNumber" @click="wxLogin">微信登录</button>
			</view> -->
		</view>
		<!-- 视频精选 -->
	  <view class="section-box" style="width: 100%;" v-if="videobool == 1">
		<view style="width: 50%;float: left;">
			<text class="decoration"></text>
			<text class="section-text">视频精选</text>
		</view>
		<view style="width: 50%;float: right;text-align: right;">
			<text class="" style="text-align: right;color:red"  >
				 
			</text>
			<view @tap="requestSubscribeMessage" class="" style="background-color: #C9A6A6;width: 200rpx;height: 50rpx;text-align: center;line-height: 50rpx;border-radius: 30rpx;float: right;">
				打卡
			</view>
		</view>
		<!-- <text class="" style="text-align: right;color:red" @tap="getShow">
			手动获取订阅消息
		</text> -->
	  </view>
	
	  <view class="videoSelection" v-if="videobool == 1">
		  <view v-for="video in selectedVideos" :key="video._id" class="video-item" @click="goVideoDetail(video._id, video.title)">
			<image class="video-thumbnail" :src="video.cover.url" mode="aspectFill" :draggable="false" />
			<text class="video-title">{{ video.title }}</text>
		  </view>
		</view>
		
		<!-- 遮罩层 -->
		<view class="mask" v-show="showMask" @click="hidePopup"></view>  
		<!-- 弹出层 -->  
		<view class="popup" v-show="showPopup" > 
			<view class="des">
				请输入问卷编号：
			</view>
			 <view class="input">
				<input type="text"  class="form121"  value="1" v-model="wen_id"/>
			 </view>
			 <view class="but">
					<button @click="sub">确定</button>
			 </view>
		</view> 
	</view>
</template>

<script>
	// #ifdef APP-PLUS
	import statusBar from "@/uni_modules/uni-nav-bar/components/uni-nav-bar/uni-status-bar";
	// #endif
	import {
		store,
		mutations
	} from '@/uni_modules/uni-id-pages/common/store.js'
	export default {
		// #ifdef APP-PLUS
		components: {
			statusBar
		},
		// #endif
		data() {
			return {
				quan:0,
				videobool:0,
				wen_id:'',
				showMask: false, // 控制遮罩层显示与隐藏
				showPopup: false, // 控制弹出层显示与隐藏  
				current: 0,
				hasLogin:false,
				bannerList : [],
				videoList: [], // 添加视频列表数据
				selectedVideos: [], // 存储随机选择的视频
			}
		},
		onShow() {
			this.hasLogin = uniCloud.getCurrentUserInfo().tokenExpired > Date.now()
			console.log(uniCloud.getCurrentUserInfo())
		},
		onLoad() {
			 
			
			this.videoOff()
			this.getBanner()
			this.getVideos() // 获取视频列表
			this.getWenId()
			// this.requestSubscribeMessage()
		},
		methods: {
			videoOff(){
				uni.request({
					url:"https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/banner/videoOff"
				}).then(res=>{
					this.videobool = res[1].data
					this.quan = 1
				})
			},
			getShow(){
				this.getBanner()
			},
			//订阅消息
			requestSubscribeMessage() {
				var tmp1 = 'M5t4KOuKxEh_w8bkDY40MrEc2LZKNLC5md4U_wCmRRg';
				var tmp2 = 'ejc2fjY4BZ7JqDuYV4rW4x2byGybZhCw009N4d7AhW4';
				var tmp3 = '7_tDJv2IAYHG3xE-qp54HqZDPQMvjMDMGTBYesSbqrI';
				uni.requestSubscribeMessage({
					tmplIds: [tmp1,tmp2,tmp3], // 改成你的小程序订阅消息模板id
					success: (e) => {
						// if(e.M5t4KOuKxEh_w8bkDY40MrEc2LZKNLC5md4U WCmRRg){
							
						// }
						// uni.showToast({
						// 	title: "订阅成功",
						// 	icon: "none"
						// })
					} 
				});
			   
			},
			//调用
			sendPushMessage(){
				uni.request({
					url:"https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/banner/pushMessage"
				}).then(res=>{})
			},
			
			//发送通知
			showPopup1() {
			  this.showMask = true;  
			  this.showPopup = true;  
			},  
			hidePopup1() {  
			  this.showMask = false;  
			  this.showPopup = false; 
			},
			sub(){
				let userInfo = uni.getStorageSync('uni-id-pages-userInfo')
				let userId = userInfo._id
				uni.request({
				 	url:"https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/banner/setWenId",
				 	method: 'POST',
				 	data:{'uid':userId,'wen_id':this.wen_id},
				 	success: (res) => {
						 this.showMask = false;
						 this.showPopup = false; 
					},
				})
			},
			//获取用户是否填写问卷编号
			getWenId(){
				let userInfo = uni.getStorageSync('uni-id-pages-userInfo')
				let userId = uniCloud.getCurrentUserInfo().uid
				console.log('用户编号',userId)
				uni.request({
				 	url:"https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/banner/getWenId",
				 	method: 'POST',
				 	data:{'uid':userId},
				 	success: (res) => {
						console.log('获取问卷数据',res)
						const user = res.data.data[0];
						 if(user != ''){
							 if('wen_id' in user){
								 if(user.wen_id == ''){
									 this.showMask = true;
									 this.showPopup = true; 
								 }
							 }else{
								 this.showMask = true;
								 this.showPopup = true; 
								 console.log('首页弹框:' +this.showMask)
							 }
						 }
					},
				})
				 
			 
			},
			//请求用户订阅消息：
			setMessage(){
			 
				uni.requestSubscribeMessage({
					tmplIds: ["zak3TIfuFoj--GetvpFyQoAG7fTXANFqHmqAA8gydcc"], // 改成你的小程序订阅消息模板id
					success: (res) => {
					 
						 console.log(res)
					}
				});
					console.log(2222)

			},
			//微信登录
			wxLogin(){
				wx.login({
					provider: 'weixin',
					success: (res) => {
						console.log('微信登录成功', res)
						this.hasLogin = true
					},
					fail: (err) => {
						console.log('微信登录失败', err)
					}
				});
			},
			
			//获取banner
			getBanner(){
				uni.request({
					url:"https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/banner/getBanner"
				}).then(res=>{
					console.log('banner数据是:',res[1].data.data)
				 
					this.bannerList = res[1].data.data
					//做一个banner的状态判断,如果是false则剔除数据列表
					// this.bannerList = this.bannerList.filter(item => item.status == true)
					
					console.log(this.bannerList)
				})
			},
			
// 获取视频列表
			getVideos(){
				let userInfo = uni.getStorageSync('uni-id-pages-userInfo')
				let userId = uniCloud.getCurrentUserInfo().uid
				 
				uni.request({
				 	url: 'https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/push/getSurveysId',
				 	method: 'POST',
				 	data:{'uid':userId},
				 	success: (res) => {
						if(res.data != ''){
							uni.request({
								url: 'https://fc-mp-f02adfe0-c2b7-4979-95b9-97f95913e417.next.bspapp.com/push/getAllVideoList',
								method: 'GET',
								data:{category_id:res.data},
								success: (res) => {
									console.log('ss',res)
									let videoList = res.data.data
									let arr = []
									videoList.forEach((item) => {
										if (!item.start_score) {
											arr.push(item)
										}
									})
									const newData = arr.map(item => {
										const newItem = {
											...item,
											url: item.videofile.url,
										};
										delete newItem.videofile;
										return newItem;
									});
									this.videoList.unshift(...newData)
									this.selectRandomVideos() // 随机选择视频
								}
							})
						}
					},
				})
				 
			},
						
			// 随机选择2-3个视频
			selectRandomVideos() {
				let count = Math.min(this.videoList.length, 4)
				this.selectedVideos = this.videoList.sort(() => 0.5 - Math.random()).slice(0, count)
			},
			created() {
			    this.getAllVideo();
			  },
			
			//去消息通知列表
			goList(){
				console.log('去消息通知列表')
				uni.switchTab({
					url: '/pages/list/list'
				});
			},
			
			//去问卷列表
			goQuestion(){
				uni.navigateTo({
					url: '/pages/questionsList/questionsList'
				});
			},
			
			// 点击视频进入详细页面
			goVideoDetail(videoId, title) {
				console.log(`Navigating to video detail: ${videoId}, ${title}`);
					uni.navigateTo({
						url: `/pages/list/detail?id=${videoId}&title=${title}`
					});
			}, 
			
			
			
			
			change(e) {
				uni.showToast({
					title:this.$t('grid.clickTip') + " " + `${e.detail.index + 1}` + " " + this.$t('grid.clickTipGrid'),
					icon: 'none'
				})
			},
			/**
			 * banner加载后触发的回调
			 */
			onqueryload(data) {
			},
			changeSwiper(e) {
				this.current = e.detail.current
			},
			/**
			 * 点击banner的处理
			 */
			clickBannerItem(item) {
				// 有外部链接-跳转url
				if (item.open_url) {
					uni.navigateTo({
						url: '/uni_modules/uni-id-pages/pages/common/webview/webview?url=' + item.open_url + '&title=' + item.title,
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
				}
				// 其余业务处理
			}
		}
	}
</script>

